import React from "react"
import Img from "gatsby-image"
import { graphql } from "gatsby"
import Helmet from "react-helmet"
import Layout from "../components/layout"

export default ({ data }) => (
    <Layout>
        <Helmet
            bodyAttributes={{
                class: "page herminius",
            }}
            htmlAttributes = {
                {
                    lang: "en"
                }
            }
        />

        <div className="banner">
            <h1>Herminius</h1>
            <Img fluid={data.herminius.childImageSharp.fluid} alt="The Embarkation of the Queen of Sheba, Claude Lorrain, 1648" />
        </div>

        <section>
            <p>
                This client came to us to refresh their current website, it was previously a one-page static site that simply opened onto a full-screen image, with heavy images and unoptimized code. As it was a static site the client was unable to update any of the content themselves, which was a requirement for the new site.
            </p>

            <div className="page__image-wrapper">
                <Img fluid={data.herminiusOld.childImageSharp.fluid} alt="Screenshot of Herminius' old site" />
                <Img fluid={data.herminiusNew.childImageSharp.fluid} alt="Screenshot of Herminius' new site" />
            </div>

            <div className="text-center">
                <p>
                    <a href="https://herminius.com" target="_blank" rel="noopener noreferrer">herminius.com</a>
                </p>

                <p>
                    <small>
                        The old site is still available via <a href="https://web.archive.org/web/20181216150242/https://herminius.com/" target="_blank" rel="noopener noreferrer">Wayback Machine</a>
                    </small>
                </p>
            </div>


            <p>
                From the outset the site was built with speed and performance in mind, keeping the page weight and requests as low as possible. All the images are served up via srcset and the website attempts to use .webp if the browser supports it (falls back to png / jpg where applicable).
            </p>

            <p>
                As budget constraints are always against us we weren't able to include some nice to have features that would have improved the usability of the site, such as preloading an internal page when the user hovers over the page link or lazy loading images. However, with that being said most pages still fully load in ~500ms.
            </p>
        </section>
    </Layout>
)

export const pageQuery = graphql`
    query {
        herminius: file(relativePath: { eq: "herminius.jpg" }) {
            childImageSharp {
                fluid(quality: 100, maxWidth: 2560) {
                    ...GatsbyImageSharpFluid_withWebp
                }
            }
        }
        herminiusOld: file(relativePath: { eq: "herminius-old.jpg" }) {
            childImageSharp {
                fluid(quality: 100, maxWidth: 956) {
                    ...GatsbyImageSharpFluid_withWebp
                }
            }
        }
        herminiusNew: file(relativePath: { eq: "herminius-new.jpg" }) {
            childImageSharp {
                fluid(quality: 100, maxWidth: 956) {
                    ...GatsbyImageSharpFluid_withWebp
                }
            }
        }
    }
`;